
import React, { Component } from "react";
import ReactDOM from 'react-dom';
import { Input, FormBtn } from "../../components/Form";
import {
  Accordion,
  AccordionItem,
  AccordionItemTitle,
  AccordionItemBody,
} from 'react-accessible-accordion';
import 'react-accessible-accordion/dist/fancy-example.css';
import API from "../../utils/API";
import { Link } from "react-router-dom";
import styles from "./Home.css";
import Slideshow from "../../components/Slideshow/Slideshow";

class Home extends Component {
  state = {
    firstName: "",
    lastName: "",
    email: "",
    password: "",
    id: "",
    beerNum: 0
  };

  componentDidMount() {

  }

  handleLoginSubmit = event => {
    event.preventDefault();
    if (this.state.email && this.state.password) {
      API.loginUser({
        email: this.state.email,
        password: this.state.password
      })
        .then(res => {
          this.setState({
            firstName: res.data.firstName,
            lastName: res.data.lastName,
            email: res.data.email,
            password: res.data.password,
            id: res.data._id,
            beerNum: res.data.beerNum
          })
          sessionStorage.setItem("firstName", this.state.firstName);
          sessionStorage.setItem("lastName", this.state.lastName);
          sessionStorage.setItem("beerNum", this.state.beerNum);
          sessionStorage.setItem("email", this.state.email);
          sessionStorage.setItem("password", this.state.password);
          sessionStorage.setItem("id", this.state.id);
          window.location = "/dashboard";
          console.log(this.state);
        })
        .catch(err => console.log(err));
    }
  };

  handleRegisterSubmit = event => {
    event.preventDefault();
    if (this.state.firstName && this.state.lastName && this.state.email && this.state.password) {
      API.registerUser({
        firstName: this.state.firstName,
        lastName: this.state.lastName,
        email: this.state.email,
        password: this.state.password,
        beerNum: 0
      })
        .then(res => {
          this.setState({
            id: res.data._id,
            beerNum: 0
          })
          sessionStorage.setItem("firstName", this.state.firstName);
          sessionStorage.setItem("lastName", this.state.lastName);
          sessionStorage.setItem("beerNum", this.state.beerNum);
          sessionStorage.setItem("email", this.state.email);
          sessionStorage.setItem("password", this.state.password);
          sessionStorage.setItem("id", this.state.id);
          window.location = "/dashboard";
          console.log(this.state);
        })
        .catch(err => console.log(err));
    }
  };

  handleInputChange = event => {
    const { name, value } = event.target;
    this.setState({
      [name]: value
    });
  };

  render() {
    return (
      <div>

        <Accordion className="accordion">
          <AccordionItem>
            <AccordionItemTitle className="sign-up">
              <p><i className="fas fa-beer"></i> Sign Up Now</p>
            </AccordionItemTitle>
            <AccordionItemBody >
              <form className="body">
                <Input
                  className="input"
                  value={this.state.firstName}
                  onChange={this.handleInputChange}
                  name="firstName"
                  placeholder="First Name (required)"
                />
                <Input
                  className="input"
                  value={this.state.lastName}
                  onChange={this.handleInputChange}
                  name="lastName"
                  placeholder="Last Name (required)"
                />
                <Input
                  className="input"
                  value={this.state.email}
                  onChange={this.handleInputChange}
                  name="email"
                  placeholder="Email (required)"
                />
                <Input
                  className="input"
                  value={this.state.password}
                  onChange={this.handleInputChange}
                  name="password"
                  placeholder="Password (required)"
                />
                <FormBtn
                  disabled={!(this.state.firstName && this.state.lastName && this.state.email && this.state.password)}
                  onClick={this.handleRegisterSubmit}
                >
                  Submit
              </FormBtn>
              </form>
            </AccordionItemBody>
          </AccordionItem>
        </Accordion>

        <Accordion className="accordion">
          <AccordionItem>
            <AccordionItemTitle className="log-in">
              <p><i className="fas fa-beer"></i> Login To Your Dashboard</p>
            </AccordionItemTitle>
            <AccordionItemBody >
              <form className="body">
                <Input

                  className="input"
                  value={this.state.email}
                  onChange={this.handleInputChange}
                  name="email"
                  placeholder="email"
                />
                <Input
                  className="input"
                  value={this.state.password}
                  onChange={this.handleInputChange}
                  name="password"
                  placeholder="password"
                />
                <FormBtn
                  disabled={!(this.state.email && this.state.password)}
                  onClick={this.handleLoginSubmit}
                >
                  Submit
              </FormBtn>
              </form>
            </AccordionItemBody>
          </AccordionItem>
        </Accordion>

        <Slideshow />

        <p className="info-text">Was the awesome slideshow and cool graphics not enough to convince you to sign up for this awesome service? Do you need more information on how it works?<a className="info-link" href="/info"> Click here</a></p>
      </div>
    );
  }
}

export default Home;



